  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body,
html {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Protest Strike", sans-serif;
}

header {
  margin-bottom: 20px;
  padding: 20px 60px;
  width: 1000px;
  display: flex;
  justify-content: flex-end;
  box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
}

header > button {
  padding: 10px 20px;
  background-color: transparent;
  border: none;
  font-size: 20px;
  font-family: "Protest Strike", sans-serif;
  box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
  cursor: pointer;
  -webkit-user-select: none;
          user-select: none;
}

header > button:hover {
  box-shadow: 0px 5px 10px 2px rgba(23, 112, 180, 0.6);
  transition: all 300ms ease;
}

.modal_window_wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(4, 10, 41, 0.6);
  width: 100%;
  height: 100%;
  z-index: 100;
  display: none;
  animation: open 400ms ease-in-out;
}

@keyframes open {
  0% {
    display: none;
    opacity: 0;
  }
  1% {
    display: flex;
    opacity: 0;
  }
  100% {
    display: flex;
    opacity: 1;
  }
}
.active {
  display: flex;
}

.modal_window {
  color: black;
  width: 500px;
  height: 500px;
  text-align: center;
  background-color: #ffffff;
  padding: 40px 20px;
}

.hidden {
  overflow: hidden;
}

input {
  outline: none;
  border: none;
}

textarea {
  outline: none;
  border: none;
  resize: none;
}

.form_title {
  display: block;
  font-size: 30px;
  color: #333;
  line-height: 1.2;
  text-align: center;
  margin-bottom: 20px;
}

.wrap_input {
  width: 100%;
  position: relative;
  border-bottom: 2px solid #adadad;
  margin-bottom: 37px;
}

.input {
  font-size: 15px;
  color: #555;
  line-height: 1.2;
  display: block;
  width: 100%;
  height: 45px;
  background: 0 0;
  padding: 0 5px;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 390px;
  background: #fff;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.1);
  padding: 20px 10px;
}

.container a {
  position: relative;
  width: 160px;
  height: 60px;
  display: inline-block;
  background: #fff;
  margin: 20px;
}

.container a:before,
.container a:after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  transition: 0.5s;
  background: #f00;
}

.container a:nth-child(1):before,
.container a:nth-child(1):after {
  background: linear-gradient(45deg, #00ccff, #0e1538, #d400d4);
}

.container a:nth-child(2):before,
.container a:nth-child(2):after {
  background: linear-gradient(45deg, #f0075b, #0e1538, #1aff22);
}

.container a:hover:before {
  top: -3px;
  right: -3px;
  bottom: -3px;
  left: -3px;
}

.container a:hover:after {
  top: -3px;
  right: -3px;
  bottom: -3px;
  left: -3px;
  filter: blur(10px);
}

.container a span {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: inline-block;
  background: #0e1538;
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2em;
  text-transform: uppercase;
  letter-spacing: 2px;
  color: #fff;
  border: 1px solid #040a29;
  overflow: hidden;
}

.container a span::before {
  content: "";
  position: absolute;
  top: 0;
  left: -50%;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.075);
  transform: skew(25deg);
